        <b-col class="text-left" cols="1">
          <a href="#"><i data-feather="facebook"></i></a>
        </b-col>
        <b-col class="text-left" cols="1">
          <a href="#"><i data-feather="instagram"></i></a>
        </b-col>
        <b-col class="text-left" cols="1">
          <a href="#"><i data-feather="mail"></i></a>
        </b-col>
        <b-col class="text-right" cols="3" offset="6">
          <span>Zenwood &copy 2018</span>
        </b-col>
      </b-row>
    </div>
  </footer>
</template>
<script>
  export default {
    name: 'BottomFooter',
    mounted () {
      this.$feather.replace()
    }
  }
</script>
<style scoped>
  a {
    color: #505050;
  }
  a:hover {
    color: black;
  }
</style>



